import { FC, ReactNode } from 'react'
import { RouteConfig } from '@primer/types'
import NavList from '@primer/components/NavList'
import PageContent from '@primer/components/PageContent'
import { CardWrapper } from '@primer/components/Card'
import clsx, { ClassValue } from 'clsx'
import useLocalStorage from '@primer/hooks/useLocalStorage'

interface PageNavbarProps {
	links: RouteConfig[]
	className?: ClassValue
	children: ReactNode
}

const PageNavbar: FC<PageNavbarProps> = ({ links, className, children }) => {
	const [min, setMin] = useLocalStorage('case-min', false)

	return (
		<PageContent className={className}>
			<CardWrapper className="relative">
				<div className="max-w-full">
					<div
						className={clsx(
							'grid grid-cols-1 max-w-full',
							min ? 'md:grid-cols-[62px,minmax(0,1fr)]' : 'md:grid-cols-[20rem,minmax(0,1fr)]'
						)}
					>
						<div className="p-3">
							<NavList links={links} min={min} />
						</div>

						<div className="relative mt-6 lg:mt-0 border-t lg:border-t-0 lg:border-l border-gray-200 dark:border-gray-600">
							<div className="absolute top-0 left-0 h-full group z-10">
								<div
									className={clsx(
										'w-1 -ml-0.5 h-full bg-primary-600 opacity-0 group-hover:opacity-100 transition-opacity duration-100 ease-linear',
										min ? 'cursor-right' : 'cursor-left'
									)}
									onClick={() => setMin(!min)}
								/>
							</div>
							<div>{children}</div>
						</div>
					</div>
				</div>
			</CardWrapper>
		</PageContent>
	)
}

export default PageNavbar
