import { FC, ReactNode } from 'react'
import clsx, { ClassValue } from 'clsx'
import { isAnchorRouteConfig, isLinkRouteConfig, isNotNone, RouteConfig } from '@primer/types'
import { LinkGetProps, Link } from '@reach/router'
import { ExternalLinkIcon } from '@heroicons/react/outline'
import Tooltip from './Tooltip'

type IntentValue = 'primary' | 'secondary' | 'white'

const intents = {
	primary: {
		wrapper: 'bg-gray-800 dark:bg-gray-900 antialiased',
		link: {
			active: 'text-white bg-gray-700 dark:bg-gray-700 focus:bg-gray-900 dark:focus:bg-gray-800',
			inactive:
				'text-gray-200 hover:text-white hover:bg-gray-900 focus:text-white focus:bg-gray-700 dark:hover:bg-gray-800',
		},
		toggle: 'text-gray-400 hover:text-gray-300 hover:bg-gray-700',
	},
	secondary: {
		wrapper: 'bg-white dark:bg-gray-700 antialiased shadow',
		link: {
			active: 'text-primary-900 dark:text-gray-100 bg-primary-100 dark:bg-gray-800 focus:bg-gray-200 dark:focus:bg-gray-800',
			inactive:
				'text-gray-600 dark:text-gray-200 hover:text-gray-900 dark:hover:text-gray-200 dark:hover:bg-gray-600 hover:bg-primary-50 focus:text-gray-900 focus:bg-gray-100 dark:focus:bg-gray-800 dark:focus:text-gray-200',
		},
		toggle: 'text-gray-400 hover:text-gray-500 hover:bg-gray-100',
	},
	white: {
		wrapper: 'bg-white antialiased shadow',
		link: {
			active: 'text-gray-900 bg-gray-50 focus:bg-gray-200',
			inactive: 'text-gray-600 hover:text-gray-900 hover:bg-gray-100 focus:text-gray-900 focus:bg-gray-100',
		},
		toggle: 'text-gray-400 hover:text-gray-500 hover:bg-gray-100',
	},
}

interface NavListProps {
	links: RouteConfig[]
	intent?: IntentValue
	min?: boolean
	className?: ClassValue
}

const NavList: FC<NavListProps> = ({ links, intent = 'secondary', min = false, className = null }) => {
	return (
		<nav className={clsx('space-y-1', className)}>
			{links.filter(isNotNone).map((link, i) => (
				<NavLink key={i} link={link} intent={intent} min={min} />
			))}
		</nav>
	)
}

interface NavLinkProps {
	link: RouteConfig
	intent: IntentValue
	min?: boolean
}

const NavLink: FC<NavLinkProps> = ({ link, intent, min = false }) => {
	return (
		<div>
			{link && (
				<Tooltip message={link.tooltip || link.title} enabled={min} intent="dark">
					<MenuLink route={link} intent={intent}>
						<div className="opacity-75 pointer-events-none mr-3">{link.icon}</div>
						<div className={clsx(min && 'lg:hidden')}>{link.title}</div>
					</MenuLink>
				</Tooltip>
			)}
		</div>
	)
}

const getProps =
	(route: RouteConfig, intent: IntentValue) =>
	({ isCurrent, isPartiallyCurrent }: LinkGetProps) => {
		return (route.exact ? isCurrent : isPartiallyCurrent)
			? {
					className: clsx(
						'group flex items-center p-2 text-sm font-medium leading-5 rounded-l rounded-r transition ease-in-out duration-150 focus:outline-none focus:ring-2 focus:ring-primary-500',
						intents[intent].link.active
					),
			  }
			: {
					className: clsx(
						'group flex items-center p-2 text-sm font-medium leading-5 rounded-l rounded-r transition ease-in-out duration-150 focus:outline-none focus:ring-2 focus:ring-primary-500',
						intents[intent].link.inactive
					),
			  }
	}

const MenuLink = ({ route, intent, children }: { route: RouteConfig; intent: IntentValue; children: ReactNode }) => {
	return isAnchorRouteConfig(route) ? (
		<a
			href={route.url}
			className={clsx(
				'group flex items-center justify-center p-2 text-sm leading-5 rounded-l rounded-r transition ease-in-out duration-150 focus:outline-none focus:ring-2 focus:ring-primary-500',
				intents[intent].link.inactive,
				'justify-center'
			)}
			target="_blank"
			rel="noopener noreferrer"
		>
			<div className="flex items-center justify-center">{children}</div>

			<div className="flex-1 flex justify-end">
				<ExternalLinkIcon className="opacity-25 w-4 h-4" />
			</div>
		</a>
	) : isLinkRouteConfig(route) ? (
		<Link to={`${process.env.PUBLIC_URL}${route.to}`} getProps={getProps(route, intent)}>
			{children}
		</Link>
	) : null
}

export default NavList
