import { ReactNode, FC } from 'react'
import useLocalStorage from '@primer/hooks/useLocalStorage'
import NavList, { NavListItems } from './NavList'
import clsx, { ClassValue } from 'clsx'

interface SidebarProps {
	items: NavListItems | ((min: boolean) => NavListItems)
	header?: ReactNode | ((min: boolean) => ReactNode)
	footer?: ReactNode | ((min: boolean) => ReactNode)
	name?: string
	minimizable?: boolean
	className?: ClassValue
}

const Sidebar: FC<SidebarProps> = ({
	items,
	header = null,
	footer = null,
	name = 'sidebar',
	minimizable = false,
	className,
}) => {
	const [min, setMin] = useLocalStorage(name, false)

	return (
		<div
			className={clsx(
				'h-full flex flex-col bg-gray-800 relative transition-all duration-100',
				min ? 'w-16' : 'w-64',
				className
			)}
		>
			{minimizable && (
				<div
					className={clsx('absolute right-0 top-0 h-full -mr-2 w-4 z-40 group', {
						'cursor-right': min,
						'cursor-left': !min,
					})}
					onClick={() => setMin((x) => !x)}
				>
					<div className="group-hover:opacity-100 opacity-0 transition-opacity ease-in-out duration-200 w-1 h-full ml-1.5 bg-primary-600" />
				</div>
			)}

			<div className="flex-1 flex flex-col overflow-scroll">
				{typeof header === 'function' ? header(min) : header}

				<div className="px-3 flex-1">
					<NavList items={typeof items === 'function' ? items(min) : items} />
				</div>

				{typeof footer === 'function' ? footer(min) : footer}
			</div>
		</div>
	)
}

export default Sidebar
