import { FC, Suspense, useMemo, useRef } from 'react'
import { RouteComponentProps, Link } from '@reach/router'
import logo from 'assets/img/logo-text.svg'
import Loader from '@primer/components/Loader'
import Topbar from '@primer/components/Topbar'
import OnRouteChange from '@primer/components/OnRouteChange'
import MobileMenu from '@primer/components/MobileMenu'
import { isNotNone } from '@primer/types'
import { CogIcon, QuestionMarkCircleIcon, ViewGridIcon } from '@heroicons/react/outline'
import db from 'db'

const PageTopbar: FC<RouteComponentProps> = ({ children }) => {
	const ref = useRef<HTMLDivElement>(null)

	const entities = db.entities.all()

	const links = useMemo(
		() => [
			{
				title: 'Home',
				to: '/home',
				icon: <ViewGridIcon className="w-5 h-5" />,
				exact: true,
			},
			...entities.map((entity) => ({
				title: entity.plural || entity.name,
				to: `/${entity.key}`,
				icon: <QuestionMarkCircleIcon className="w-5 h-5" />,
				exact: true,
			})),
			{
				title: 'Settings',
				to: '/settings',
				icon: <CogIcon className="w-5 h-5" />,
				exact: false,
			},
		],
		[entities]
	)

	return (
		<>
			<div className="min-h-screen bg-gray-200 dark:bg-gray-800">
				<MobileMenu links={links.filter(isNotNone)} withTopbar />

				<div className="bg-gray-800 dark:bg-gray-900 hidden md:block">
					<nav className="bg-gray-800 dark:bg-gray-900">
						<div className="max-w-7xl mx-auto sm:px-6 lg:px-8">
							<div className="flex items-center justify-between h-16 px-4 sm:px-0">
								<div className="flex items-center">
									<div className="flex-shrink-0">
										<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
											<img src={logo} alt="ICRA" className="h-10" />
										</Link>
									</div>
									<div className="hidden md:block">
										<div className="ml-10 flex items-baseline space-x-4">
											{links.filter(isNotNone).map((x, i) => (
												<Link
													key={i}
													to={`${process.env.PUBLIC_URL}${x.to}`}
													className="text-gray-300 hover:bg-gray-700 hover:text-white px-3 py-2 rounded-md text-sm font-medium"
												>
													{x.title}
												</Link>
											))}
										</div>
									</div>
								</div>
								<div className="hidden md:block">
									<Topbar intent="dark" />
								</div>
							</div>
						</div>
					</nav>
				</div>

				<main ref={ref} className="relative z-0">
					<Suspense
						fallback={
							<div className="flex justify-center items-center h-screen">
								<Loader />
							</div>
						}
					>
						<div className="relative z-20">{children}</div>
					</Suspense>
				</main>
			</div>

			<OnRouteChange
				action={() => {
					window.scrollTo(0, 0)
					ref?.current?.scrollTo(0, 0)
				}}
			/>
		</>
	)
}

export default PageTopbar
