import { ReactNode, FC, Fragment } from 'react'
import { None } from '@primer/types'
import { Link } from '@reach/router'
import clsx from 'clsx'

interface NavItemType {
	title: ReactNode
	icon?: ReactNode
}

interface NavLink extends NavItemType {
	to: string
	exact?: boolean
}
const isNavLink = (x: any): x is NavLink => x.to !== undefined

interface NavAnchor extends NavItemType {
	href: string
}
const isNavAnchor = (x: any): x is NavAnchor => x.href !== undefined

interface NavButton extends NavItemType {
	onClick: () => void
}
const isNavButton = (x: any): x is NavButton => x.onClick !== undefined

export type NavItem = NavLink | NavAnchor | NavButton | None

export interface NavGroup {
	title?: ReactNode
	items: NavItem[]
}
const isNavGroup = (x: any): x is NavGroup => x.items !== undefined

export type NavListItems = (NavItem | NavGroup | None)[]

interface NavListProps {
	items: NavListItems
}

const NavList: FC<NavListProps> = ({ items }) => {
	const renderItem = (item: NavItem) =>
		isNavLink(item)
			? renderLink(item)
			: isNavAnchor(item)
			? renderAnchor(item)
			: isNavButton(item)
			? renderButton(item)
			: null

	const renderLink = (item: NavLink) => (
		<Link
			to={`${process.env.PUBLIC_URL}${item.to}`}
			getProps={({ isCurrent, isPartiallyCurrent }) => ({
				className: clsx(
					(item.exact ? isCurrent : isPartiallyCurrent)
						? 'bg-gray-600 text-gray-100'
						: 'text-gray-300 hover:bg-gray-700',
					'flex items-center px-2.5 py-2 text-sm font-medium rounded'
				),
			})}
		>
			{item.icon && <div className=" mr-3">{item.icon}</div>}
			<span className="truncate antialiased">{item.title}</span>
		</Link>
	)

	const renderAnchor = (item: NavAnchor) => (
		<a
			href={item.href}
			className="flex items-center px-2.5 py-2 text-sm font-medium rounded text-gray-300 hover:bg-gray-700"
		>
			{item.icon && <div className=" mr-3">{item.icon}</div>}
			<span className="truncate antialiased">{item.title}</span>
		</a>
	)

	const renderButton = (item: NavButton) => (
		<button
			onClick={item.onClick}
			className="flex items-center w-full px-2.5 py-2 text-sm font-medium rounded text-gray-300 hover:bg-gray-700"
		>
			{item.icon && <div className=" mr-3">{item.icon}</div>}
			<span className="truncate antialiased">{item.title}</span>
		</button>
	)

	return (
		<nav className="space-y-1" aria-label="Sidebar">
			{items.map((item, i) =>
				isNavGroup(item) ? (
					<div key={i}>
						<div className={clsx('mt-4 pt-5', i !== 0 && 'border-t border-gray-500')}>
							{item.title && (
								<div className="px-3 mb-2 text-xs font-semibold text-gray-400 uppercase tracking-wider antialiased">
									{item.title}
								</div>
							)}
							<div className="space-y-1">
								{item.items.map((item, i) => (
									<Fragment key={i}>{renderItem(item)}</Fragment>
								))}
							</div>
						</div>
					</div>
				) : (
					<Fragment key={i}>{renderItem(item)}</Fragment>
				)
			)}
		</nav>
	)
}

export default NavList
